import { useState } from "react";
import { describeClue, clue } from "./clue";

interface ShareProps {
  guesses: string[],
  target: string,
  dark: boolean
}

export function Share(props: ShareProps) {
  const [ copied, setCopied ] = useState(false); 
  const handleOnClick = () => {
    const cantShare = (e: any) => navigator.userAgent.toLowerCase().indexOf("firefox") > -1 || void 0 === navigator.share || !navigator.canShare || !navigator.canShare(e);

    let string = `Footbl 5 1 ${props.guesses.length}/6\n\n`;
    
    props.guesses.forEach(guess => {
      const t = describeClue(clue(guess, props.target));
      t.split(",").forEach(l => {
        if (l.endsWith("no")) {
          string += props.dark ? "" : "";
        } else if (l.endsWith("elsewhere")) {
          string += ""
        } else if (l.endsWith("correct")) {
          string += ""
        }
      });
      string += `\n`;
    });

    const data = { text: string };

    if (cantShare(data)) {
      navigator.clipboard.writeText(string);
      setCopied(true);
    } else {
      navigator.share(data);
    }
  }
  
  return (
    <>
      <button 
        style={{ 
          marginBottom: "20px", 
          fontSize: "20px", 
          backgroundColor: "#00acee", 
          color: "white",
          border: "2px solid rgba(0, 0, 0, 0.3)",
          padding: "5px 10px",
          fontWeight: "bold"
        }} 
        onClick={handleOnClick}>
        SHARE
      </button>
      {copied ? <h3>Copied results to your clipboard</h3> : null}
    </>
  );
}
